body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #eff3f7;
}
html, #root {
  height: 100%;
}
code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
    padding: 50px;
    display: flex;
    justify-content: space-around;
    flex-wrap: wrap;
    font-size: 14px;
}

.App--loading{
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100%;
}

.Movie {
    background-color: white;
    width: 40%;
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    flex-wrap: wrap;
    margin-bottom: 50px;
    text-overflow: ellipsis;
    padding: 0 20px;
    box-shadow: 0 8px 38px rgba(133,133,133,0.3), 0 5px 12px rgba(133,133,133, 0.22);
}
.Movie__Columns {
    width: 30%;
    box-sizing: border-box;
    text-overflow: ellipsis;
}
.Movie__Columns:last-child{
    padding: 20px 0;
    width: 60%;
}
.Movie h1 {
    font-size: 20px;
    font-weight: 600;
}
.Movie__Genres .Movie__Genres {
    margin-right: 10px;
    color:#b4b5bd;
}
.Movie .Movie__Synopsis {
    text-overflow: ellipsis;
    color:#b4b5bd;
    overflow:hidden;
}
.Movie .Movie__Poster {
    max-width: 100%;
    position: relative;
    top:-20px;
    box-shadow: -10px 19px 38px rgba(83,83,83,0.3), 10px 15px 12px rgba(80,80,80,0.22);
}

@media screen and (min-width:320px) and (max-width:667px){
    .Movie {
        width: 100%;
    }
}
@media screen and (min-width:320px) and (max-width:667px) and (orientation: portrait){
    .Movie{
        width: 100%;
        flex-direction: column;
    }
    .Movie__Poster {
        top: 0;
        left: 0;
        width: 100%;
    }
    .Movie__Column {
        width: 100% !important;
    }
}
